.mainMaterial {
    position: absolute;
    left: 31%;
    display: flex;
    flex-direction: column;
    font-family: 'Quicksand', sans-serif;
    background: rgb(83,125,204);
    background: linear-gradient(360deg, rgba(83,125,204,1) 0%, rgba(255,255,255,1) 90%);
    width: 69%;
    height: 680px;
  }

.listMaterial {
  margin-left: 1rem;
  display: flex;
  flex-direction: row;
}

.itemMaterial {
  margin-right: 1rem;
}

.buttonMaterial {
  margin-right: 2rem;
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.buttonMaterial:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

.divButton {
  margin-right: auto;
  margin-left: auto;
}