@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
/* Universal Styles */

html {
  font-size: 18px;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Quicksand', sans-serif;
}

h1 {
  padding: .66rem 0;
  text-align: center;
  background-color: #537dcc;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.8rem;
  color: #fff;
}
.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url(/bygningspas/static/media/concrete-texture-bg.95396712.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 12rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem .33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #537dcc;
  color: #537dcc;
}

.SearchHeader {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #282c34;
}

.SearchBar-submit {
  text-align: center;
  margin-top: 25px;
}

.SearchBar-submit a {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/bygningspas/static/media/background_search_mobile.a6f8ee50.jpg);
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}
.Building {
  display: flex;
  flex-direction: column;
  font-family: 'Quicksand', sans-serif;
  background: rgb(83,125,204);
  background: linear-gradient(360deg, rgba(83,125,204,1) 0%, rgba(255,255,255,1) 90%);
  width: 31%;
  height: 680px;
}

.Building .image-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  margin-top: 1rem;
  max-width: 30rem;
  max-height: auto;
  border-style: solid;
  border-width: 2px;
}

.Building h2 {
  margin-left: 1rem;
  position: relative;
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Building-information {
  display: flex;
  justify-content: space-between;
}

.Building-information p {
  font-size: .88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Building-address {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mainMaterial {
    position: absolute;
    left: 31%;
    display: flex;
    flex-direction: column;
    font-family: 'Quicksand', sans-serif;
    background: rgb(83,125,204);
    background: linear-gradient(360deg, rgba(83,125,204,1) 0%, rgba(255,255,255,1) 90%);
    width: 69%;
    height: 680px;
  }

.listMaterial {
  margin-left: 1rem;
  display: flex;
  flex-direction: row;
}

.itemMaterial {
  margin-right: 1rem;
}

.buttonMaterial {
  margin-right: 2rem;
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.buttonMaterial:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

.divButton {
  margin-right: auto;
  margin-left: auto;
}
